*{
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
}

body{
  padding: 20%;
}

input{
  color: grey;
  width:100% ;
  font-size: 1.3rem;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius:  6px;
  overflow: hidden;
  margin-bottom:  10px;
  margin-right: 10px;

}

.card p{
  padding: 5px;
  text-align: center;


}

.card img {
  max-height: 180px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}
